.header-prt {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.link-container {
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin-top: 100px;
  margin-bottom: 100px;
  display: flex;
}

.link-container .link {
  color: gray;
  font-size: 60px;
  text-decoration: none;
  transition: transform .3s ease-in-out;
}

.link-container .link:hover {
  transform: scale(1.1);
}

.input-div {
  justify-content: center;
  align-items: center;
  display: flex;
}

.input {
  width: 600px;
  height: 60px;
  color: #474545;
  text-align: center;
  border: 2px solid gray;
  outline: none;
  padding: 0 20px;
  font-size: 40px;
  display: block;
}

.input::-ms-input-placeholder {
  text-align: center;
  color: #9b9393;
}

.input::placeholder {
  text-align: center;
  color: #9b9393;
}

.btn {
  width: 64px;
  height: 64px;
  color: #474545;
  cursor: pointer;
  border: 2px solid gray;
  border-left: none;
  outline: none;
  font-size: 18px;
  transition: all .3s ease-in-out;
  display: block;
}

.btn:hover {
  color: #fff;
  background-color: #716d6d;
}

.li-btn {
  width: 150px;
  height: 40px;
  color: #474545;
  cursor: pointer;
  background-color: #e7a146;
  border: none;
  border-radius: 4px;
  transition: all .3s ease-in-out;
}

.li-btn:hover {
  color: #000;
  background-color: #e38d1d;
  transform: scale(1.02);
}

